import {categoryList} from '../../mock/datas/index.js'
export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			// flist: [],
			slist: [], // 右边商品
			// tlist: [],
			// cateList:cateList,
			categoryList:categoryList
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		 loadData(){
			this.categoryList.forEach(item=>{
				item.data.forEach((k,i,ary)=>{
					// this.$set(ary,'pid',item.cid)
					k.pid = item.cid
					this.slist.push(k)
				})
			})
			
		},
		//一级分类点击
		tabtap(item){
			// 计算每个的高度，让后定位到哪里， 直接修改 scroll-top 的高度
			if(!this.sizeCalcState){
				this.calcSize();
			}
			
			this.currentId = item.cid;
			let index = this.slist.findIndex(sitem=>sitem.pid === item.cid);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			
			let h = 0;
			this.slist.forEach((item,index)=>{
				let view = uni.createSelectorQuery().select("#main-" + index);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(keyword){
			// 直接搜索
			uni.navigateTo({
				url: `/pages/goods/goods?keyWork=${keyword}`
			})
		}
	}
}
